    height: 100vh;
} */
.form-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    margin-top: 60px;
}
.form-box {
    border-radius: 15px;
    box-shadow: rgba(0, 0, 0, 0.3) 10px 10px 3.5px;
}
.logo {
    height: 100px;
    width: 100%;
    margin-bottom: 10px;
}
.list-data {
    margin-top: 110px;
    box-shadow: rgba(0, 0, 0, 0.3) 10px 10px 3.5px;
}
.name {
    margin-top: 110px;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

